}

/* Tags Container - Hardware Accelerated */
.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem; /* Reduced from 0.8rem to 0.6rem */
  margin-bottom: 1.2rem; /* Reduced from 2rem to 1.2rem */
      
  .tag {
    background: rgba(0, 0, 0, 0.8);
    color: #ffffff;
    padding: 0.5rem 1rem;
    border-radius: 25px;
    font-size: 0.85rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
    // Use specific transitions for better performance
    transition: background-color 0.15s ease, border-color 0.15s ease;
    
    // Add hardware acceleration
    will-change: background-color, border-color;
    contain: layout style paint;
    
    &:hover {
      background: rgba(255, 94, 0, 0.2);
      border-color: rgba(255, 94, 0, 0.5);
    }
  }
}

/* Explore Button Styling - Hardware Accelerated */
.explore-btn {
  background: linear-gradient(45deg, #6c757d, #495057);
  color: #ffffff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  // Use specific transitions for better performance
  transition: transform 0.15s ease, box-shadow 0.15s ease, background 0.15s ease;
  box-shadow: 0 4px 15px rgba(124, 58, 237, 0.3);
  
  // Add hardware acceleration
  will-change: transform, box-shadow;
  contain: layout style paint;
      
  &:hover {
    // Use translate3d for hardware acceleration
    transform: translate3d(0, -2px, 0);
    box-shadow: 0 8px 25px rgba(108, 117, 125, 0.4);
    background: linear-gradient(45deg, #495057, #6c757d);
  }
  
  &:active {
    transform: translate3d(0, 0, 0);
  }
  
  svg {
    transition: transform 0.15s ease;
    // Add hardware acceleration for SVG
    will-change: transform;
  }
  
  &:hover svg {
    // Use translate3d for hardware acceleration
    transform: translate3d(4px, 0, 0);
  }
}

